@import '../../common/colors';

.Gameboard {
    h1 {
        color: #43a047;
    }

    .Stats {
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: flex-start;
        padding-top: 5px;
        color: white;
        font-size: 20px;
        font-weight: bold;
        .Level {
            padding: 0.25rem 1rem;
            background-color: #654321;
            color: white;
            border-top-right-radius: 1rem;
            border-bottom-right-radius: 1rem;
        }

        .Progress {
            text-align: right;
            padding: 0.25rem 1rem;
            background-color: #654321;
            color: white;
            border-top-left-radius: 1rem;
            border-bottom-left-radius: 1rem;

            .WordStats {
                font-weight: normal;
                font-size: 16px;
            }
        }
    }

    display: flex;
    align-items: center;
    height: 100vh;
    flex-direction: column;

    .GridSection {
        height: 50vh;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;

        .CurrentWord {
            height: 28px;
            font-size: 20px;
            margin-bottom: -30px;
        }

        .Grid {
            display: flex;
            flex-direction: column;

            .Row {
                display: flex;

                .Cell {
                    text-align: center;
                    border: 3px solid #634820;
                    background-color: #966F3355;
                    border-radius: 1rem;
                    font-weight: bold;
                    cursor: pointer;
                    user-select: none;
                    margin: 5px;

                    &.Selected {
                        color: white
                    }
                }
            }
        }

        .Message {
            opacity: 1;
            transition: opacity 0.5s;
            font-size: 18px;
            color: #856404;
            text-align: center;
            background-color: #fff3cd;
            padding: 0.5rem 1rem;
            margin-bottom: 20px;
            border-radius: 1rem;
            text-transform: uppercase;
            &.Animated {
                animation: PopInOut 2s linear  forwards;
            }
        }
    }

    .FoundWords {

        height: 30vh;
        padding-top: 2rem;
        width: 100%;
        text-align: center;

        h2 {
            font-size: 20px;
            font-weight: normal;
            text-decoration: underline;
            text-transform: uppercase;
        }

        .Words {
            display: flex;
            flex-wrap: wrap;
            align-items: flex-start;
            align-content: flex-start;
            .Word {
                display: inline-block;
                width: 50%;
                font-weight: bold;
                animation: FadeIn 0.5s linear forwards;
            }
        }
    }

    .LevelComplete {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #0008;
        z-index: 100;

        .Content {
            position: fixed;
            top: 50%;
            left: 50%;
            text-align: center;
            transform: translateX(-50%) translateY(-50%);
            background: linear-gradient(to bottom right, $success 40%, $secondary 100%);
            padding: 1rem;
            color: #155724;
            border-radius: 1rem;
            width: 80%;
            max-width: 500px;
            animation: PopIn 1s linear  ;

            h3 {
                color: black;
                text-transform: uppercase;
            }

            img {
                height: 64px;
                margin-top: -20px;
                animation: bounce 1s ease-in infinite;
            }

            .Proceed {
                background-color: #e0a800;
                display: inline-block;
                border-radius: 20px;
                padding: 0.5rem 1rem;
                font-weight: bold;
                text-transform: uppercase;
                margin-top: 1rem;
                color: black;
                cursor: pointer;
                box-shadow: 2px 2px 10px rgba(119, 119, 119, 0.5);
            }
        }
    }

    @keyframes bounce {
        50% {
            transform: translateY(-10px);
        }
    }

    @keyframes PopIn {
        0% {
            opacity: 0;
        }

        20% {
            transform: scale(1.1)  translateX(-45%) translateY(-45%);
            opacity: 1;
        }

        100% {
            transform: scale(1)  translateX(-50%) translateY(-50%);
        }
    }

    @keyframes FadeIn {
        0% {
            opacity: 0;
            transform: scale(1.5) translateY(-100%);
        }
        100% {
            opacity: 1;
            transform: scale(1);
        }
    }

    @keyframes PopInOut {
        0% {
            opacity: 0;
            transform: scale(0.5);
            transform-origin: center;
        }

        20% {
            opacity: 1;
            transform: scale(1);
        }

        80% {
            opacity: 1;
        }
        100% {
            opacity: 0;
        }
    }
}